
* {
  margin: 0;
  padding: 0;
}
html,
body {
  background: #51b8cb;
}
#app {
  width: 10rem;
  height: 100%;
  overflow: hidden;
  margin: 0 auto;
}
.qrcodeContain {
  width: 9.333333rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-size: 0.453333rem;
  background: #fff;
  margin: 0.666667rem 0.4rem;
  padding-bottom: 0.533333rem;
  letter-spacing: 1px;
  border-radius: 0.2rem;
  -webkit-box-shadow: 0 0.133333rem 0.133333rem #888888;
          box-shadow: 0 0.133333rem 0.133333rem #888888;
